import React from 'react';
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { Link } from "gatsby"

const AboutPage = () => {
  const data = useStaticQuery(graphql`
    query {
      logo: file(relativePath: { eq: "logo.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <div className="about-blurb">
      <div className="container">
        <div className="inner-blurb">
          <div className="content">
            <h3>About Me</h3>
            <p>Hello! My name is Claire, and I am currently a third-year at the
            University of California, Berkeley, studying data science and statistics.
            You can view my resume below, and feel free to reach out to me
            via claire.chour@berkeley.edu!
            </p>
            <div className="btn-row">
              <Link to="/resume">View Resume</Link>
            </div>
          </div>
          <div className="images">
            <div className="top-right">
              <Img fluid={data.logo.childImageSharp.fluid} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default AboutPage
